@import url(https://fonts.googleapis.com/css2?family=Syne+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:"Syne Mono",monospace}button{font-family:"Syne Mono",monospace}h1,h2,h3,h4,h5,h6,nav{font-family:"Major Mono Display",monospace}.dashboard{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-flex-wrap:nowrap;flex-wrap:nowrap;justify-items:stretch;height:100vh}.dashboard header{-webkit-flex:0 0 auto;flex:0 0 auto;background-color:#282C34;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;color:white;padding:32px}.dashboard header a{text-decoration:none;color:white}.dashboard header h1{margin:0}.dashboard header nav{font-size:1.5em}.dashboard header nav ol{margin:0;list-style-type:none}.dashboard header nav ol li{display:inline;padding:0 16px}.dashboard>div{-webkit-flex:1 0 auto;flex:1 0 auto}.dashboard>div.overview{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;padding:32px;-webkit-align-items:flex-start;align-items:flex-start}.dashboard>div.overview>div{-webkit-flex:0 0 20%;flex:0 0 20%;border:1px solid #282C34;padding:16px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.dashboard>div.overview>div h2{-webkit-flex:0 0 auto;flex:0 0 auto;margin:0}.dashboard>div.overview>div ul{list-style-type:none;padding:0;-webkit-align-self:stretch;align-self:stretch}.dashboard>div.two-panel{display:-webkit-flex;display:flex;-webkit-align-items:stretch;align-items:stretch}.dashboard>div.two-panel>div.tabs-panel{padding:0;margin:0;-webkit-flex:0 0 35%;flex:0 0 35%;border-right:1px solid #282C34;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:stretch;align-items:stretch}.dashboard>div.two-panel>div.tabs-panel .tabs-container{display:-webkit-flex;display:flex}.dashboard>div.two-panel>div.tabs-panel .tabs-container>.tab{-webkit-flex:1 1 auto;flex:1 1 auto;border:1px solid white;background-color:#d3d3d3;cursor:pointer;-webkit-flex-wrap:wrap;flex-wrap:wrap;padding:8px 16px}.dashboard>div.two-panel>div.tabs-panel .tabs-container>.tab.current{pointer-events:none;background-color:dimgray;color:white}.dashboard>div.two-panel>div.tabs-panel>.tab-content{-webkit-flex:1 0 auto;flex:1 0 auto}.dashboard>div.two-panel>div.main-panel{-webkit-flex:1 0 auto;flex:1 0 auto}.dashboard>div.two-panel .factory{width:300px;height:300px;border:1px solid blue;background:transparent}

